/* style */
body {
    background: #eeeeee;
}
table {
    border-collapse: collapse;
    margin: 40px auto;
    text-align: center;
}
table td, table th {
    border: 1px solid #cad9ea;
    color: #666;
    height: 30px;
}
table thead th {
    background-color: #CCE8EB;
    width: 100px;
}
table tr:nth-child(odd) {
    background: #fff;
}
table tr:nth-child(even) {
    background: #F5FAFA;
}
.requestProcess {
    display: flex;
    justify-content: center;
    align-items: center;
}
.requestResource {
    display: flex;
    justify-content: center;
    align-items: center;
}
.requestResource button {
    white-space: nowrap;
    padding: 5px 10px;
    border: none;
    border-radius: 4px;
    background: #0282B9;
    color: white;
    font-size: 12px;
}
.requestResource .res {
    width: 50px;
}